import React, { useEffect, useState } from 'react';

import {toast} from "react-toastify";
import {REFER_LINK, WALLET_ADDRESS} from "../../config/config";
import {showError} from "../../config/helpers";
import useActiveWeb3React from "../../hooks/useWeb3";


const ReferCard = () => {
    const { account } = useActiveWeb3React()
    const [isLoading, setLoading] = useState(true);
    const [referLink, setRefer] = useState('');

    const [open, setOpen] = React.useState(false);
    const [transition, setTransition] = React.useState(undefined);
    const [message, setMessage] = useState('')
    const getReferL = () => {
        const nowAddress = account;
        if(!account){
            showError("Connect wallet!!")
            return
        }
        setRefer(REFER_LINK+nowAddress);
    }
    const getReferLink = async () => {
        const nowAddress = account;
        if(!account){
            showError("Connect wallet!!")
            return
        }
        setRefer(REFER_LINK+nowAddress);
        try{
            navigator.clipboard.writeText(REFER_LINK+nowAddress)
            toast.success('Copied Successfully!', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        } catch (e) {
            showError(`Could Not Copy To Clipboard - ${e}`)
        }
    }
    useEffect(() => {
        getReferL()
        setLoading(false);
    }, []);

    return (
        <>
        <div className="relative pt-10 pb-10 px-6 2xl:pt-20 2xl:pb-20 bg-blueGray-100 rounded-b-10xl">
            <div className="relative max-w-lg mx-auto">
                <img className="absolute top-1/2 transform -translate-y-1/2" src="images/bg-form.svg" alt="" />
                <div className="shadow-lg shadow-sky-500/40 hover:shadow-2xl bg-gradient-to-r from-sky-900 to-blue-900 bg-opacity-50 rounded-3xl py-10 lg:py-20 px-8 lg:px-10 m-5 border-t-4 border-opacity-30 border-gray-100 relative z-10">
                    <div className="w-20 h-20 md:w-40 md:h-40 bg-circle bg-white rounded-full opacity-10 absolute pin-t ml-8 p-2 z-20 top-0 right-0 -mr-14 -mt-14" />
                    <div className="w-20 h-20 md:w-40 md:h-40 bg-circle bg-white rounded-full opacity-5 absolute pin-t ml-8 p-2 z-20 bottom-0 left-0 -ml-14 -mb-14" />
                    <h3 className="text-3xl leading-10 text-white text-center mb-6">Referral Link</h3>
                    {/* <p class="text-base text-white leading-8 font-medium mb-10">Fusce sodales vitae lorem vitae now tempus.</p> */}
                    <div className="relative md:mb-8">
                        {/* <img class="z-10 absolute top-1/2 transform -translate-y-1/2 left-8" src="images/mail.svg" alt=""> */}
                        <div className="referral-link w-full text-xs text-center px-4 py-3 h-auto md:max-h-16 bg-blueGray-50 rounded-lg text-sky-900 dark:text-sky-900 border-2 border-sky-500 flex align-middle justify-center bg-white bg-opacity-80 break-words break-all">
                            {
                                referLink === '' ? "Click to generate link" : referLink
                            }
                        </div>
                    </div>
                    <a onClick={getReferLink} className="block py-4 px-6 mt-3 md:mx-auto w-full md:max-w-max text-sm leading-6 text-white font-medium tracking-tighter font-heading text-center bg-sky-700 hover:bg-sky-600 focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50 rounded-lg" href="#">
                        Generate &amp; Copy Link
                    </a>
                </div>
            </div>
            <div className="absolute w-20 transform -translate-x-1/2 xl:w-auto left-1/2 md:left-0 top-0 md:top-20 xl:top-1/2 -translate-y-1/2 flex align-middle justify-center">
                {/* <span class="absolute top-11 left-11 inline-flex h-10 w-10 rounded-full bg-white opacity-75"></span> */}
                <svg width={132} height={132} viewBox="0 0 132 132" fill="none" xmlns="http://www.w3.org/2000/svg" className="relative">
                    {/* <circle cx="66" cy="66" r="65.25" transform="rotate(-90 66 66)" fill="white" stroke="#E4EBF3" stroke-width="1.5"/> */}
                    <circle cx={66} cy={66} r={41} transform="rotate(-90 66 66)" fill="#EFF1F5" fillOpacity="0.1" className />
                    <circle cx={66} cy={66} r={21} transform="rotate(-90 66 66)" fill="white" fillOpacity="0.8" />
                    <path d="M68.8 60L73 66H59" stroke="#303030" strokeWidth={2} strokeLinecap="round" strokeLinejoin="round" />
                    <path d="M69 71L73 66" stroke="#303030" strokeWidth={2} strokeLinecap="round" strokeLinejoin="round" />
                </svg>
            </div>
        </div>
        </>
    );
};

export default ReferCard;