import React, {useEffect, useState} from "react";
// importing Link from react-router-dom to navigate to
// different end points.
import { Link } from "react-router-dom";
import Sidebar from "../Sidebar";
import useActiveWeb3React from "../hooks/useWeb3";
import {getContract} from "../config/helpers";
import {FARMING} from "../config/constants";
import {farmingABI} from "../config/abis";
import {formatEther} from "@ethersproject/units";
import ReferCard from "./referral/refercard";
import MTPLCreatePool from "./pool-mtpl";
import ConnectWallet from "../components/mtpl/ConnectWallet";
import {shortAddress} from "../utils/pageHelpers";
import MTPLProfile from "./MTPLProfile";

const MTPLReferral = () => {
    const { account } = useActiveWeb3React()

    const [farmRewards, setFarm] = useState(0);
    const [stakeRewards, setStake] = useState(0);

    const getRewards = async () => {
        try{
            if(!account) return
            const farming = await getContract(FARMING, farmingABI)
            const rewards = await farming.getRewards(account)
            // console.log(rewards)
            setFarm(rewards[0])
            setStake(rewards[1])
        }catch (e) {
            console.log(e)
        }
    }

    useEffect(() => {
        const inter = setInterval(() => getRewards(), 5000)
        return (() => clearInterval(inter))
    }, []);
    return (
        <>
            <div className='mx-auto lg:ml-64'>
                <div className='hero-section px-5 py-4 md:px-10 md:py-8 h-full relative'>
                    <nav className="flex items-center justify-between mb-4">
                        <h2 className="title text-black dark:text-white text-opacity-75 text-3xl font-bold">Referral</h2>
                        {
                            !account && <ConnectWallet />
                        }
                        {
                            account &&
                            <MTPLProfile />
                        }
                    </nav>
                    <section className="font-medium">
                        <div className="md:grid md:grid-cols-2 md:items-center">
                            <div className="container mx-auto max-w-2xl md:pb-0 pb-10 md:py-0">
                                <span className="inline-block mb-5 uppercase tracking-widest text-sm text-sky-900 dark:text-gray-300 leading-4 font-bold">Earn MLX</span>
                                <h2 className="mb-8 font-heading text-3xl xl:text-10xl leading-tight text-gray-800 dark:text-white">Invite Your Friends!</h2>
                                <h3 className="mb-5 xl:mb-10 max-w-lg font-heading text-3sm xl:text-xl leading-10 text-gray-800 dark:text-white">Earn 5% commission reward from your friend's earnings generated from Farms &amp; Staking Pools.</h3>
                                <section className="py-0 overflow-hidden">
                                    <div className="container mx-auto">
                                        <div className="grid grid-cols-1 2xl:grid-cols-2 gap-5 md:gap-4">
                                            <div className="md:block col-span-1 md:col-span-1">
                                                <div className="flex items-center">
                                                    <div className="flex-shrink-0 text-center mr-3">
                                                        <div className="h-25 w-auto flex items-center justify-center bg-sky-600 rounded-t-3xl px-4 py-6">
                                                            <div className="text-white text-xl">
                                                                {parseFloat(formatEther(farmRewards)).toFixed(4)}
                                                            </div>
                                                        </div>
                                                        <div className="py-4 px-2 bg-sky-800 rounded-b-3xl">
                                                            <span className="font-heading font-bold text-white">MLX</span>
                                                        </div>
                                                    </div>
                                                    <div>
                                                        <h3 className="mb-4 text-lg font-heading font-medium text-gray-800 dark:text-white">Farm Referral</h3>
                                                        <p className="text-gray-400 text-xs leading-6">Referral Earnings on Farm Rewards your Refer earns.</p>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="md:block col-span-1 md:col-span-1">
                                                <div className="flex items-center">
                                                    <div className="flex-shrink-0 text-center mr-3">
                                                        <div className="h-25 w-auto flex items-center justify-center bg-sky-600 rounded-t-3xl px-4 py-6">
                                                            <div className="text-white text-xl">
                                                                {parseFloat(formatEther(stakeRewards)).toFixed(4)}
                                                            </div>
                                                        </div>
                                                        <div className="py-4 px-2 bg-sky-800 rounded-b-3xl">
                                                            <span className="font-heading font-bold text-white">MLX</span>
                                                        </div>
                                                    </div>
                                                    <div>
                                                        <h3 className="mb-4 text-lg font-heading font-medium text-gray-800 dark:text-white">Staking Referral</h3>
                                                        <p className="text-gray-400 text-xs leading-6">Referral Earnings on Stake Rewards your Refer earns.</p>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </section>
                            </div>
                            <ReferCard />
                        </div>
                    </section>
                </div>
            </div>
        </>
    );
};

export default MTPLReferral;